@import './assets/scss/variables.scss';

body {
    background-color: $secondary-color;
    color: white;
}
li {
  list-style-type: none;
    white-space: nowrap;
}
button {
    white-space: nowrap;
}
a {
    color: white;
    font-weight: bold;
    &:hover {
        text-decoration: none;
    }
}
.mobile-menu {
    a {
        color: $secondary-color;
    }
}
.header-app-title {
    color: white;
}
.flex-grow-high {
    flex-grow: 15;
}
.fg-2 {
    flex-grow: 2;
}
.max-w-75 {
    max-width: 75%;
}
#root {
    min-height: 100vh;
    display: flex;
    padding-bottom: 100px;
    flex-direction: column;
}
.form-control {
    background-color: inherit;
}
svg {
    max-height: 400px;
    max-width: 100%;
}
@media screen and (max-width: 640px) {
    svg {
        max-height: 220px;
    }
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    vertical-align: baseline;
}
button:focus {
    outline: none;
}
.rdw-editor-toolbar {
    color: black;
}
.rdw-editor-main {
    border: 2px solid white;
    background-color: white;
    color: black;
    min-height: 200px;
}
.no-width {
    width: 0px;
    position: absolute;
    z-index: -1000;
    left: -9999px;
    height: 0px;
}
.full-screen-page {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    background-color: $secondary-color;
    z-index: 10000;
    height: 100vh;
    min-height: 100vh;
    padding-top: 15px;
    padding-bottom: 15px;
}
.min-full-screen-page {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    background-color: $secondary-color;
    z-index: 10000;
    min-height: 100vh;
    padding-top: 15px;
    padding-bottom: 15px;
}
.no-bold {
    font-weight: normal !important;
}
.form-center {
    margin-top: auto;
    margin-bottom: auto;
    padding-bottom: 80px;
}
